<template>
  <div>
    <b-container class="pt-4">
      <div class="large-title mt-5">{{ reglament.name }}</div>
      <button @click="$router.push(`/reglament/${taskId}`)" v-if="!currentUser.department.is_regulator" class="float-right custom-edit">Редактировать</button>
      <button @click="$router.push({ name: 'reglamentReview', params: { id:taskId } })" v-else class="float-right custom-edit">Проверить</button>
      <div class="small_grey-text mt-2 mb-5">Последние измненения: {{moment(reglament.updated_at).format('DD.MM.YYYY HH:mm')}}</div>
      <button @click="openPDF">Скачать</button>
      <div class="md-title mt-5">Отвественный сотрудник:</div>
      <UserPreview :user="reglament.created_by" />

      <!-- <div class="mt-5"><button class="custom-edit">Отправить на согласование</button></div> -->
    </b-container>

    <div class="md-text mt-5">
      <b-container class="pt-2">
        <div class="md-title mb-3">Этапы согласований:</div>
      </b-container>
      <b-container >
        <b-row v-for="(department) in reglament.departments_users" :key="department.user.department.id" class="task-item w-100" align-v="center">
          <b-col cols="8" class="justify-content-between d-flex align-items-center">
            <div class=" d-flex align-items-center">
              <!-- bg-red -->
              <div class="dot-wrapper mr-3 pl-1 pt-1"><div class="large-dot"></div></div>
              <!-- text-red -->
              <div class="text-blue ml-3">{{department.status || 'Ожидание'}}: {{department.user.department.name}}</div>
            </div>

            <div class="mt-2 cursor-pointer" @click="gotoIssue(department)" :class="{'issue-alert': hasIssue(department)}"><b-icon class="float-right icon-lg" icon="chat-left-text"></b-icon></div>
          </b-col>
          <b-col cols="2" class="text-right small_grey-text">
            {{department.user.last_name ? department.user.last_name  : ""}}
            {{department.user.first_name ? department.user.first_name[0] + '. ': ""}}
            {{department.user.patronymic_name ? department.user.patronymic_name[0] + '.': ""}}
          </b-col>
          <b-col cols="2" class="text-right small_grey-text">
            01.10.2020
          </b-col>
        </b-row>
      </b-container>
    </div>

    <div class="md-text mt-5 pt-5">
      <b-container>
        <div class="md-title mb-2 pt-4">Изменения:</div>
      </b-container>
      <b-container>
        <b-row class="w-100 task-item" align-v="center">
          <b-col cols="8" class="justify-content-between d-flex align-items-center">
            <div class=" d-flex align-items-center">
              <div class="dot-wrapper mr-3 pl-1 pt-1"> <div class="large-dot"></div> </div>
              <div class="text-blue ml-3">Создание текста регламента</div>
            </div>
          </b-col>
          <b-col cols="2" class="text-right small_grey-text">
            Иванов И.И
          </b-col>
          <b-col cols="2" class="text-right small_grey-text">
            01.10.2020
          </b-col>
        </b-row>
      </b-container>
    </div>
  </div>
</template>

<script>
import mainService from '../sevices/mainService'
import { mapGetters } from 'vuex'
import UserPreview from "../components/UserPreview";
import moment from 'moment';

export default {
  name: 'Task',
  components: {
    UserPreview
  },
  props: {
      taskId: {
          required: true,
          type: String
      }
  },
  data: () => ({
    reglament: {},
    revisions: [],
    moment
  }),
  created() {
    this.getReglament();
    this.getRevisions();
  },
  computed: {
    ...mapGetters([
      'currentUser',
    ])
  },
  methods: {
    hasIssue({user}) {
      return this.revisions.some(revision => {
        return !revision.is_marked_solved && revision.created_by.username === user.username;
      })
    },
    gotoIssue(department) {
      if (this.hasIssue(department)) {
        this.$router.push(`/revisions/${this.reglament.id}/${department.user.id}`);
      }
    },
    getReglament() {
      mainService.getReglament(this.taskId)
        .then(({data}) => {
          this.reglament = data[0];
        })
    },
    getRevisions() {
      mainService.getRevisions(this.taskId)
        .then(({data}) => {
          this.revisions = data.results;
        })
    },
    openPDF() {
      window.open(`/api/pdf?id=${this.taskId}`)
    }
  }
}
</script>

<style lang="scss" scoped>
  .issue-alert{
    position: relative;
    &::after{
      content: "";
      display: inline-block;
      width: 10px;
      height: 10px;
      background: red;
      border-radius: 50%;
      position: absolute;
      right: -2px;
      top: -2px;
    }

  }

  .task-item{
    height: 68px;
    border-bottom: 1px solid #D9D9DE;
  }
  .custom-edit{
    margin-top: -40px;
  }
</style>
